/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
@import url("https://fonts.googleapis.com/css?family=Open+Sans:300,400,700&subset=cyrillic,cyrillic-ext");
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

* {
  font-family: 'Open Sans', sans-serif; }

.application {
  min-height: 100vh;
  background-color: #070600; }
  .application .navigation-wrapper {
    position: fixed;
    min-height: 100vh;
    width: 100vw;
    -webkit-transform: translateX(-100%);
        -ms-transform: translateX(-100%);
            transform: translateX(-100%);
    background-color: #279AF1;
    -webkit-transition: -webkit-transform ease 600ms;
    transition: -webkit-transform ease 600ms;
    -o-transition: transform ease 600ms;
    transition: transform ease 600ms;
    transition: transform ease 600ms, -webkit-transform ease 600ms;
    z-index: 999;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    max-width: 500px;
    will-change: transform;
    max-height: 100vh;
    overflow: auto;
    height: 100vh; }
  .application .navigation-wrapper-inner {
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column; }
  .application .application-wrapper {
    opacity: 1;
    background-color: #F7F7FF;
    min-height: 100vh;
    width: 100vw;
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);
    -webkit-transition: opacity ease 700ms, -webkit-transform ease 700ms;
    transition: opacity ease 700ms, -webkit-transform ease 700ms;
    -o-transition: transform ease 700ms, opacity ease 700ms;
    transition: transform ease 700ms, opacity ease 700ms;
    transition: transform ease 700ms, opacity ease 700ms, -webkit-transform ease 700ms;
    will-change: opacity, transform; }

.application_nav-active .navigation-wrapper {
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0); }

.application_nav-active .application-wrapper {
  opacity: 0.4;
  -webkit-transform: scale(0.8);
      -ms-transform: scale(0.8);
          transform: scale(0.8);
  overflow: hidden;
  max-height: 100vh; }



