  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  opacity: 1;
  visibility: visible;
  -webkit-transition: opacity ease-in-out 200ms, visibility ease-in-out 200ms;
  -o-transition: opacity ease-in-out 200ms, visibility ease-in-out 200ms;
  transition: opacity ease-in-out 200ms, visibility ease-in-out 200ms;
  z-index: 99999;
  color: #000; }

.dialog_inactive {
  opacity: 0;
  visibility: hidden; }

.dialog__window {
